<template>
  <v-card class="signup-page">
    <div>
      <img src="../assets/logo.png">
      <div class="" v-if="showsignup">
        <p class="signup-title">注册用户</p>
        <v-form direction="horizontal">
            <v-form-item label="账号" :label-col="labelCol" :wrapper-col="wrapperCol">
                <v-input type="text" placeholder="用户手机" size="large" v-model="req.account"></v-input>
            </v-form-item>
            <v-form-item label="用户名" :label-col="labelCol" :wrapper-col="wrapperCol">
                <v-input type="text" placeholder="用户名（3-8个数字或字母组成）" size="large" v-model="req.nickname"></v-input>
            </v-form-item>
            <v-form-item label="邮箱" :label-col="labelCol" :wrapper-col="wrapperCol">
                <v-input type="text" placeholder="邮箱（用户找回密码）" size="large" v-model="req.mail"></v-input>
            </v-form-item>
            <v-form-item label="密码" :label-col="labelCol" :wrapper-col="wrapperCol">
                <v-input  type="password" placeholder="密码（8-12位数字加字母组成）" size="large" v-model="req.password"></v-input>
            </v-form-item>
            <v-form-item label="重复密码" :label-col="labelCol" :wrapper-col="wrapperCol">
                <v-input  type="password" placeholder="再次输入密码" size="large" v-model="chkPwd"></v-input>
            </v-form-item>
            <v-form-item :wrapper-col="{offset:6, span: 14 }">
                <v-button style="margin-right:8px" type="primary" @click.prevent="SignUp()">提交</v-button>
                <v-button style="margin-left:8px" @click="toLogin()">取消</v-button>
            </v-form-item>
        </v-form>
      </div>
      <div v-else>
        <p class="signup-title">注册成功</p>
        <img src="">
        <p>您注册的账号为{{req.account}}</p>
        <v-button type="dashed" style="margin-top:24px" @click="toLogin()">去登录</v-button>
      </div>
    </div>
  </v-card>
</template>

<script>
export default {
  data () {
    return {
      req: {
        account: "",
        nickname: "",
        mail: "",
        password: ""
      },
      chkPwd: "",
      showsignup: true,
      labelCol: {
        span: 6
      },
      wrapperCol: {
        span: 14
      }
    }
  },
  methods: {
    SignUp() {
      if(this.req.nickname.length < 3 || this.req.nickname.length > 8) {
        this.$message['error']("用户名由3-8个数字或字母组成");
        return false;
      } else if(this.req.password.length < 8 || this.req.password.length > 12) {
        this.$message['error']("密码由8-12个数字或字母组成");
        return false;
      } else if (this.req.password != this.chkPwd) {
        this.$message['error']("输入的密码不一致");
        return false;
      }
      let api = '/api/newAccount';
      this.$http.post(api, this.req).then(res => {
        if(res.data.code == 1) {
          this.$message['error']("该账号已被注册");
        } else if(res.data.code) {
          this.$message['error']("注册失败");
        } else {
          this.showsignup = false;
          this.newAcnt = res.data.account;
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      })
    },
    toLogin() {
      this.$router.push({path: '/Login'});
    }
  }
}
</script>

<style scoped>
.signup-page {
  width: 100%;
  height: 100%;
  display:flex;
  justify-content:center;
  align-items:center;
}

.signup-title {
  font-size: 28px;
  font-weight: bold;
  margin: 12px 0;
}
</style>



